import { useState } from "react";

export default function ContactsFilterCards() {
  const CardData = [
    {
      id: 1,
      office: "Main Headquarters",
      title: "123 Estatein Plaza, City Center, Metropolis",
      text: "Our main headquarters serve as the heart of Estatein. Located in the bustling city center, this is where our core team of experts operates, driving the excellence and innovation that define us.",
      mail: "info@estatein.com",
      phone: "+1 (123) 456-7890",
      address: "Metropolis",
      category: "International",
    },
    {
      id: 2,
      office: "Regional Offices",
      title: "456 Urban Avenue, Downtown District, Metropolis",
      text: "Estatein's presence extends to multiple regions, each with its own dynamic real estate landscape. Discover our regional offices, staffed by local experts who understand the nuances of their respective markets.",
      mail: "info@restatein.com",
      phone: "+1 (123) 628-7890",
      address: "Metropolis",
      category: "Regional",
    },
  ];

  const [filter, setFilter] = useState("All");

  const filterCards = CardData.filter(
    (card) => filter === "All" || card.category === filter
  );

  return (
    <div className="lg:px-20 md:px-12 mt-10 px-6 ">
      <div className="flex md:flex-nowrap flex-wrap md:gap-10 gap-2 bg-zinc-950 w-fit py-4 px-4 rounded-lg">
        <button
          onClick={() => setFilter("All")}
          className={`text-white md:px-12 md:py-3 px-6 py-2 rounded-lg border-2 border-zinc-700 ${filter === "All" ? "bg-black" : ""}`}
        >
          All
        </button>
        <button
          onClick={() => setFilter("Regional")}
          className={`text-white md:px-12 md:py-3 px-6 py-2 rounded-lg border-2 border-zinc-700 ${filter === "Regional" ? "bg-black" : ""}`}
        >
          Regional
        </button>
        <button
          onClick={() => setFilter("International")}
          className={`text-white md:px-12 md:py-3 px-6 py-2 rounded-lg border-2 border-zinc-700 ${filter === "International" ? "bg-black" : ""}`}
        >
          International
        </button>
      </div>

      <div className="md:flex gap-5 mt-10 overflow-hidden">
        {filterCards.map((card) => (
          <div
            data-aos="flip-right"
            key={card.id}
            className="basis-1/2 border-2 mt-10 md:m-0 px-6 py-6 border-zinc-900 rounded-lg md:px-10 md:py-7"
          >
            <p className="text-white py-2">{card.office}</p>
            <h3 className="text-white text-2xl font-bold py-2">{card.title}</h3>
            <p className="text-gray-400 py-2">{card.text}</p>

            <div className="flex flex-wrap gap-2 pt-5">
              <div className="flex items-center gap-2 bg-zinc-900 border-2 border-zinc-700 py-2 px-3 rounded-full">
                <img src={require("../images/Mail-1.png")} alt="mail-icon" />
                <p className="text-white">{card.mail}</p>
              </div>
              <div className="flex items-center gap-2 bg-zinc-900 border-2 border-zinc-700 py-2 px-3 rounded-full">
                <img src={require("../images/Phone.png")} alt="phone-icon" />
                <p className="text-white">{card.phone}</p>
              </div>
              <div className="flex items-center gap-2 bg-zinc-900 border-2 border-zinc-700 py-2 px-3 rounded-full">
                <img src={require("../images/location-icon.png")} alt="location-icon" />
                <p className="text-white">{card.address}</p>
              </div>
            </div>
            <button className="text-white w-full text-center mt-5 bg-purple-700 py-2 rounded-lg">
              <a
                href="https://www.google.com/maps"
                target="_blank"
                rel="noreferrer"
              >
                Get Direction
              </a>
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}
  