import { useEffect } from "react";

export default function HeroSectionPages({ text, title }) {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="overflow-hidden">
       <div
      className="bg-gradient-to-r  from-zinc-900 to-black md:px-28 md:py-20 px-5 py-10 mt-16"
      data-aos="fade-left"
    >
      <h1 className="text-white text-2xl md:text-4xl font-semibold">{text}</h1>
      <p className="text-gray-400 pt-6">{title}</p>
    </div>
    </div>
   
  );
}
