import bgImg from "../images/bg-grid-card.png";
import bgWork1 from "../images/work-1.png";
import bgWork2 from "../images/work-2.png";
import bgWork3 from "../images/work-3.png";
import bgWork4 from "../images/work-4.png";
import bgWork5 from "../images/work-5.png";
import bgWork6 from "../images/work-7.png";

export default function ContactsPhotos() {
  return (
    <div className="md:mt-20 mt-10 md:px-12 lg:px-20 px-7 overflow-hidden">
      <div
        className="py-10 lg:px-10 px-4"
        style={{ backgroundImage: `url(${bgImg})` }}
      >
        <div>
          <div className="grid grid-cols-4 md:grid-rows-6 grid-rows-8 gap-4">
            <div
              className="col-span-2 row-span-2 bg-cover bg-center bg-no-repeat rounded-lg  md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork1})` }}
              data-aos="flip-right"
            ></div>
            <div
              className="col-span-2 row-span-2 col-start-3 bg-cover bg-center bg-no-repeat rounded-lg   md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork2})` }}
              data-aos="flip-right"
            ></div>
            <div
              className="col-span-2 row-span-2 row-start-3 bg-cover bg-center bg-no-repeat rounded-lg  md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork3})` }}
              data-aos="flip-right"
            ></div>
            <div
              className="row-span-2 col-start-3 row-start-3 bg-cover bg-center bg-no-repeat rounded-lg   md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork4})` }}
              data-aos="flip-right"
            ></div>
            <div
              className="row-span-2 col-start-4 row-start-3 bg-cover bg-center bg-no-repeat rounded-lg   md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork5})` }}
              data-aos="flip-right"
            ></div>
            <div className="md:col-span-2 row-span-2 md:row-start-5 col-span-4 row-start-5 bg-cover bg-center bg-no-repeat v  md:h-[250px] h-[160px]">
              <div
                className="flex flex-col w-full h-full justify-center gap-5  md:py-4 "
                data-aos="flip-right"
              >
                <h2 className="text-white text-2xl font-semibold">
                  Explore Estatein's World
                </h2>
                <p className="text-gray-500">
                  Step inside the world of Estatein, where professionalism meets
                  warmth, and expertise meets passion. Our gallery offers a
                  glimpse into our team and workspaces, inviting you to get to
                  know us better.
                </p>
              </div>
            </div>
            <div
              className="md:col-span-2 row-span-2 md:col-start-3 md:row-start-5 col-span-4 row-start-7 bg-cover bg-center bg-no-repeat rounded-lg  md:h-[250px] h-[150px]"
              style={{ backgroundImage: `url(${bgWork6})` }}
              data-aos="flip-right"
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
}
