export default function ContactForm() {
  return (
    <div className=" lg:px-10 md:px-5 mt-10 md:m-0 px-3 overflow-hidden">
      <form className="border-2 border-zinc-900 lg:px-10 md:px-5 px-3 rounded-lg pb-10" data-aos="fade-left">
        <div className="md:flex lg:gap-10 md:gap-5 items-center">
          <div className="pt-10 md:basis-1/3 ">
            <label className="text-white" htmlFor="name">
              First Name
            </label>{" "}
            <br />
            <input
              className="bg-zinc-900 pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="name"
              type="text"
              placeholder="Enter First Name"
              required
            />
          </div>
          <div className="pt-10 md:basis-1/3">
            <label className="text-white" htmlFor="LastName">
              Last Name
            </label>{" "}
            <br />
            <input
              className="bg-zinc-900 pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="LastName"
              type="text"
              placeholder="Enter Last Name"
              required
            />
          </div>
          <div className="pt-10 md:basis-1/3">
            <label className="text-white" htmlFor="Email">
              Email
            </label>{" "}
            <br />
            <input
              className="bg-zinc-900 pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="Email"
              type="text"
              placeholder="Enter your Email"
              required
            />
          </div>
        </div>

        <div className="md:flex lg:gap-10 md:gap-5 items-center">
          <div className="pt-10 md:basis-1/3 ">
            <label className="text-white" htmlFor="Phone">
              Phone
            </label>{" "}
            <br />
            <input
              className="bg-zinc-900 pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="Phone"
              type="text"
              placeholder="Enter Phone Number"
              required
            />
          </div>
          <div className="pt-10 md:basis-1/3">
            <label className="text-white" htmlFor="InquiryType">
              Inquiry Type
            </label>{" "}
            <br />
            <select
              className="bg-zinc-900 border-none pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="InquiryType"
              required
            >
              <option>Select Inquiry Type</option>
              <option>General Questions</option>
              <option>Interested in a Property</option>
              <option>Schedule a Viewing</option>
              <option>Financing and Mortgage Assistance</option>
            </select>
          </div>
          <div className="pt-10 md:basis-1/3">
            <label className="text-white" htmlFor="HowDidYouHearAboutUs?">
              How Did You Hear About Us?
            </label>{" "}
            <br />
            <select
              className="bg-zinc-900 border-none pl-4  mt-6 w-full rounded-lg outline-none py-4 text-white"
              id="HowDidYouHearAboutUs?"
              required
            >
              <option value="">select</option>
              <option value="">Search Engine (Google, Bing, etc.)</option>
              <option value="">Social Media (Facebook, Instagram, etc.)</option>
              <option value="">Referral from Friend or Family</option>
              <option value="">Advertisement (Online, TV, Print)</option>
            </select>
          </div>
        </div>
        <div className="mt-10">

        <label className="text-white" htmlFor="Message">Message</label> <br />
        <textarea className="w-full resize-none pl-2 py-2 pr-10 text-white mt-6 md:min-h-[200px] min-h-[150px]  bg-zinc-900 outline-none rounded-lg " placeholder="Enter your Message here.." name="Message" id="Message"></textarea>
        </div>
        <div className="pt-10 md:flex md:flex-wrap items-center justify-between">
                    <div className="md:pb-6">
                      <input required id="checkbox" type="checkbox" />
                      <label htmlFor="checkbox" className="text-gray-500 pl-2">
                        I agree with Terms of Use and Privacy Policy
                      </label>
                    </div>

                    <button
                      type="submit"
                      className="bg-violet-700 md:px-4 md:py-4 px-3 py-3 mt-4 md:w-auto w-full ml-auto  md:mr-3 lg:mt-0 rounded-md text-white"
                    >
                      Send Your Message
                    </button>
                  </div>
      </form>
    </div>
  );
}
