import { useState } from "react";
import { Link } from "react-router-dom";

export default function Header() {
  const [isVisible, setIsVisible] = useState(false);

  const handleLinkClick = () => {
    setIsVisible(false);
  };

  return (
    <>
      {/* navigation bar starts */}
      <nav className="lg:flex  items-center justify-between  lg:pb-3 bg-zinc-800 py-4 px-4 lg:px-20 fixed z-40 w-full top-0">
        {/* hamburger icon */}
        <div
          className="absolute mt-2 flex flex-col items-end cursor-pointer right-2 lg:hidden"
          onClick={() => setIsVisible((prev) => !prev)}
        >
          <div
            className={`w-[35px]  transform transition-all duration-300 h-[4px] bg-white rounded-md ${
              isVisible ? "rotate-45 w-[30px] h-[2px]" : ""
            }`}
          ></div>
          <div
            className={` w-[35px]   h-[4px] bg-white rounded-md ${
              isVisible ? "-rotate-45 w-[30px]  h-[2px] " : "mt-1 "
            }`}
          ></div>
          <div
            className={`w-[20px] ${
              isVisible ? "hidden" : ""
            }  mt-1 h-[4px] bg-white rounded-md`}
          ></div>
        </div>

        {/* Logo with Name of brand */}
        <div className="flex items-center">
          <img
            className="w-[32px] md:w-auto"
            src={require("../images/Symbol.png")}
            alt="logo"
          />
          <p className="lg:text-4xl text-3xl font-semibold pl-2 text-white">
            Estatein
          </p>
        </div>

        <ul
          className={`lg:flex gap-10 py-6 md:p-0 ${
            isVisible ? "max-h-screen block" : "max-h-0 hidden overflow-hidden"
          } lg:opacity-100 lg:max-h-none transition-all duration-300 ease-in-out`}
        >
          <li className="pt-3 lg:pt-0">
            <Link
              to="/test"
              className="text-xl text-white cursor-pointer "
              onClick={handleLinkClick}
            >
              Home
            </Link>
          </li>
          <li className="pt-3 lg:pt-0">
            <Link
              to="/test/about"
              className="text-xl text-white cursor-pointer"
              onClick={handleLinkClick}
            >
              About Us
            </Link>
          </li>
          <li className="pt-3 lg:pt-0">
            <Link
              to="/test/Properties"
              className="text-xl text-white cursor-pointer"
              onClick={handleLinkClick}
            >
              Properties
            </Link>
          </li>
          <li className="pt-3 lg:pt-0">
            <Link
              to="/test/services"
              className="text-xl text-white cursor-pointer"
              onClick={handleLinkClick}
            >
              Services
            </Link>
          </li>
        </ul>

        <div
          className={`transition-all duration-300 ease-in-out ${
            isVisible
              ? "max-h-16 opacity-100"
              : "max-h-0 opacity-0 overflow-hidden"
          } lg:max-h-none lg:opacity-100 lg:block`}
        >
          <button className="bg-black px-4 py-3 rounded-lg mt-5 lg:mt-0 text-white hover:bg-white hover:text-black">
            <Link to="/test/contact" onClick={handleLinkClick}>
              {" "}
              Contact Us{" "}
            </Link>
          </button>
        </div>
      </nav>
    </>
  );
}
