import HeroSectionPages from "./HeroSectionPages";
import ContactsCards from "./ContactsCards";
import HeaderSections from "./HeaderSections";
import ContactForm from "./ContactForm";
import ContactsFilterCards from "./ContactsFilterCards";
import ContactsPhotos from "./ContactsPhotos";
import SloganDesign from "../components/SloganDesign";
export default function ContactPage() {
  return (
    <div>
      <HeroSectionPages
        text={"Get in Touch with Estatein"}
        title={
          "Welcome to Estatein's Contact Us page. We're here to assist you with any inquiries, requests, or feedback you may have. Whether you're looking to buy or sell a property, explore investment opportunities, or simply want to connect, we're just a message away. Reach out to us, and let's start a conversation."
        }
      />

      <ContactsCards />
      <HeaderSections
        title={"Let's Connect"}
        text={
          "We're excited to connect with you and learn more about your real estate goals. Use the form below to get in touch with Estatein. Whether you're a prospective client, partner, or simply curious about our services, we're here to answer your questions and provide the assistance you need."
        }
      />
      <ContactForm />
      <HeaderSections
        title={"Discover Our Office Locations"}
        text={
          "Estatein is here to serve you across multiple locations. Whether you're looking to meet our team, discuss real estate opportunities, or simply drop by for a chat, we have offices conveniently located to serve your needs. Explore the categories below to find the Estatein office nearest to you"
        }
      />
      <ContactsFilterCards />
      <ContactsPhotos />
      <SloganDesign
        text={
          "Your dream property is just a click away. Whether you're looking for a new home, a strategic investment, or expert real estate advice, Estatein is here to assist you every step of the way. Take the first step towards your real estate goals and explore our available properties or get in touch with our team for personalized assistance."
        }
        title={"Start Your Real Estate Journey Today"}
      />
    </div>
  );
}
